<template>
  <div class="tmpl">
    <div class="mui-page-content">
      <div class="mui-scroll-wrapper">
        <div class="mui-scroll">
          <ul class="mui-table-view mui-table-view-chevron">
            <li class="mui-table-view-cell mui-media">
              <a class="mui-navigate-right" href="javascript:;">
                <img class="mui-media-object mui-pull-left head-img" id="head-img" :src="face">
                <router-link :to="{name:'userinfo',query:{username:username}}">
                  <div class="mui-media-body" v-show="isLogin">
                    {{username}}
                    <p class='mui-ellipsis'>账号:{{username}}</p>
                  </div>
                </router-link>
                <div class="mui-media-body" v-show="!isLogin">
                  <router-link :to="{name:'login'}">登录</router-link>
                  <p class='mui-ellipsis'>未登录</p>
                </div>
              </a>
            </li>
          </ul>
          <ul class="mui-table-view mui-table-view-chevron">
            <li class="mui-table-view-cell">
              <a href="#account" class="mui-navigate-right">我的订单</a>
            </li>
          </ul>
          <ul class="mui-table-view mui-table-view-chevron">
            <li class="mui-table-view-cell">
              <a href="#notifications" class="mui-navigate-right">修改用户名</a>
            </li>
            <li class="mui-table-view-cell">
              <a href="#privacy" class="mui-navigate-right">修改密码</a>
            </li>
            <li class="mui-table-view-cell">
              <a href="#general" class="mui-navigate-right">绑定手机</a>
            </li>
            <li class="mui-table-view-cell">
              <a href="#general" class="mui-navigate-right">绑定邮箱</a>
            </li>
          </ul>
          <ul class="mui-table-view">
            <li class="mui-table-view-cell" style="text-align: center;">
              <a href="javascript:;" @click="logout()">退出登录</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  import Cookie from "../common/cookie.js";
    export default {
      name: "Member",
      data(){
        return{
          isLogin:false,//是否登陆
          username:'',//用户名
          face:'/static/img/default.png',//头像路径
        }
      },
      created(){
        let username = Cookie.getCookie("username");
        if(username){
          this.isLogin = true;
          this.username = username;
          let face = Cookie.getCookie("face");
          if(face){
            this.face = face;
          }else{
            Cookie.setCookie("face",this.face,7)
          }
        }
      },
      methods:{
        logout(){
          this.$axios.get(this.$host+"api/logout/"+this.username)
            .then(res=>{
              if(res.data.code == 200){
                Cookie.delCookie("username");
                Cookie.delCookie("face");
                Cookie.delCookie("token");
                Toast(res.data.msg);
                this.$router.push({
                  name:"login",
                });
              }else {
                Toast(res.data.msg);
              }
            })
        }
      }
    }
</script>
<style scoped>
  .mui-page-content {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: #efeff4;
  }
  .mui-scroll-wrapper,
  .mui-scroll {
    position: static;
    background-color: #efeff4;
  }

  .mui-page .mui-table-view:first-child {
    margin-top: 15px;
  }
  .mui-page .mui-table-view:last-child {
    margin-bottom: 30px;
  }
  .mui-table-view {
    margin-top: 20px;
  }

  .mui-table-view span{
    color: #999;
  }

  .head-img {
    width: 40px;
    height: 40px;
  }
  .update {
    font-style: normal;
    color: #999999;
    margin-right: -25px;
    font-size: 15px
  }
</style>




